import HttpClient from "./index";
import store from "../store";
export default class Axios extends HttpClient {
  constructor() {
    super(process.env.REACT_APP_BASE_URL);
    this._initializeRequestInterceptor();
  }


  _initializeRequestInterceptor = () => {
    this.instance.interceptors.request.use(this._handleRequest);
  };

  _handleRequest = async (config) => {
    let token = store.getState().user.currentUser.accessToken
    if (token)
    config.headers.Authorization = token;
    // config.headers['CSRF-TOKEN'] = Cookies.get('v2');
    return config;
  };

  callApi(url, options) {
    return this.instance({
      method: options.method,
      url: url,
      headers: options?.header ? options?.header : {},
      data: options?.body ? options?.body : undefined,
      onUploadProgress: options?.onUploadProgress ? options?.onUploadProgress : undefined
    });
  };
}
