export const validation = (data) => {
  const { email } = data;
  var errorVal = {
    email: false,
    message: ""
  }
  if (!email) {
    errorVal["email"] = true
  }
  if (email && !(/\S+@\S+\.\S+/.test(email))) {
    errorVal["email"] = true
    errorVal["message"] = "invalid email"
  }
  return errorVal
};
