import { useNavigate } from "react-router-dom";
import "./index.css"
import { validation } from "./validation"
import { useState } from "react";
import Axios from '../../api/Axios';
import { Loader } from "rsuite";
import triggerSwalAlert from "../../helpers/triggerSwalAlert";

const axios = new Axios();

const ForgetPassword = () => {

    const navigate = useNavigate();
    const [pending, setPending] = useState(false)

    const [error, setError] = useState({
        email: false,
        message: ""
    });

    const [data, setData] = useState({
        email: "",
    });

    const onChange = (value, target) => {
        setData({
            ...data,
            [target]: value,
        });

        if (error.email === true) {
            setError({
                ...error,
                [target]: false
            })
        }

    };

    const handleForgetPassword = async (e) => {
        e.preventDefault()
        const valid = validation(data)
        setError(valid)
        if (valid.email) {
            return
        }
        const options = {
            method: "POST",
            body: data
        }
        setPending(true)
        const result = await axios.callApi(`auth/forgetPassword`, options)
        if(result.status == 200){
            setPending(false)
            triggerSwalAlert("Email Send", "Password reset email has been send to your registered email id", "success")
        }
        else{
            setPending(false)
            triggerSwalAlert("Error", "Something went wrong while trying to send email to your registered id", "error")
        }
    }

    return (
        <>
        {pending ? <Loader/> : null}
        <div className='forgetPassword-wrapper bg-dark d-flex align-item-center justify-content-center w-100'>
            <div className='forgetPassword'>
                <h2 className='mb-3'>Forget Password</h2>
                <form>
                    <div className='form-group mb-2'>
                        <label htmlFor="email" className='form-label'>Registered Email Address</label>
                        <input id='email' className={`form-control ${error.email && "errorInputField"}`} required onChange={e => onChange(e.target.value, 'email')} />
                        {error.email && <div className='error'>
                            {error.message ? "Please enter a valid email" : "Please enter your email"}
                        </div>}
                    </div>
                    <div className="button-container">
                        <button className='btn btn-dark' onClick={() => { navigate("/sign-in") }}>BACK</button>
                        <button className='btn btn-dark' onClick={handleForgetPassword}>SUBMIT</button>
                    </div>
                </form>
            </div>
        </div>
        </>
    )
}

export default ForgetPassword
